.history-header {
  position: relative;
  overflow: hidden;
  // z-index: 1;

  &:after {
    content: "";
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background: rgba(#16202E, 0.75);
  }

  &-row {
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding-bottom: 50px;
    margin-left: 0;
    margin-right: 0;
    position: relative;
    z-index: 1;
  }
  &-top {
    padding-top: 130px;

    @media (max-width: 1439px) {
      padding-top: 115px;
    }
  }
  &-bottom {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
    flex: 0 0 100%;
  }
  &-btn {
    flex: 0 0 170px;
    max-width: 170px;

    @media (max-width: 1439px) {
      flex: 0 0 130px;
      max-width: 130px;
    }
  }
  &-titles {
    max-width: 1030px;

    @media (max-width: 1439px) {
      max-width: 630px;
    }
  }
  &-text {
    @include fz(42);
    margin-bottom: 30px;
    line-height: 1.1;

    @media (max-width: 1439px) {
      @include fz(32);
    }
  }

  &-bg {
    position: absolute;
    z-index: -1;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    min-width: 100%;
    min-height: 100%;
    object-fit: cover;
    object-position: center;
  }

  &-arrow {
    display: inline-block;
    width: 34px;
    height: 13px;
    cursor: pointer;

    svg {
      fill: none;
      stroke: #FFF;
      stroke-width: 2;
    }
  }
}


.ui-top-screen {
  &-title {
    max-width: 920px;
  }

  &-text {
    max-width: 1035px;
    @include fz(42);
    margin-bottom: 44px;
  }
}

.history-upper {
  text-transform: uppercase;
}

.history-body {
  padding-top: 100px;
  color: #000;
  @include fz(22);

  @media (max-width: 1439px) {
    padding-top: 50px;
    @include fz(18);
  }
}
.history-tabs {
  font-size: 0;

  li {
    display: inline-block;
    vertical-align: middle;
    margin-bottom: 15px;
    margin-right: 16px;
  }

  &-link {
    display: inline-block;
    vertical-align: middle;
    height: 78px;
    line-height: 76px;
    border: 1px dashed $secondary;
    border-radius: 40px;
    padding: 0px 32px;
    color: $secondary;
    @include fz(42);
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
    transition: color .3s, border .3s;

    &:hover,
    &.active {
      color: #000;
      border: 1px solid #000;
    }
  }

  @media (max-width: 1439px) {
    &-link {
      @include fz(32);
      height: 72px;
      line-height: 70px;
    }
  }
  @media (max-width: 1259px) {

    &-link {
      @include fz(28);
      height: 68px;
      line-height: 66px;
    }
  }
}


.page-btn-img {
  align-items: center
}


@media (max-width: 1439px) {
  .history-body {
    .node-inner {
      padding-right: 0;
    }
  }
}


.page-error {
  position: relative;
  margin-bottom: -30px;
  
  &-txt {
    flex: 0 0 calc(100% - 170px);
    max-width: calc(100% - 170px);
    padding-right: 30px;
  }

  &-flex {
    display: flex;
    justify-content: space-between;
    align-items: flex-end;
    min-height: calc(100vh - 80px);
    padding-top: 70px;
    padding-bottom: 50px;
  }

  &-bg {
    position: absolute;
    left: 0;
    right: 0;
    margin: 0 auto;
    bottom: 0;
    z-index: -1;
    display: block;
    max-height: calc(100% - 160px);
    max-width: 75%;
  }

  &-title {
    margin-bottom: 0;
    @include fz(300);
    line-height: 0.95;
    color: $primary;
  }

  &-subtitle {
    @include fz(54);
    line-height: 1;
  }

  @media (max-width: 1439px) {
    &-title {
      @include fz(240);
    }
    &-subtitle {
      @include fz(42);
    }
  }
}


//stub
.plug-mobile {
  display: none;
}

@media (max-width: 1023px) {
  html {
    overflow: hidden;
  }
  .plug-mobile {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 9999;
    display: block;
    padding-top: 100px;
    padding-bottom: 50px;
    overflow: hidden;
    background: $bgc;

    .logo-img {
      max-width: 40px;
    }

    &-header {
      padding-top: 28px;
      padding-bottom: 28px;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
    }

    &-row {
      display: flex;
      height: calc(100vh - 100px);
    }

    &-text {
      max-width: 55%;
    }

    &-title {
      margin-bottom: 57px;
      width: 400px;
      font-weight: 600;
      @include fz(60);
      line-height: 1;
    }

    &-subtitle {
      width: 280px;
      font-weight: 500;
      @include fz(24);
      line-height: 1.25;
    }
    
    &-bg {
      position: absolute;
      right: -10%;
      bottom: 0;
      max-width: 60%;
      z-index: -1;
    }
  }
}

@media (max-width: 1023px) and (orientation: portrait) {
  .plug-mobile {
    &-text {
      max-width: 100%;
    }
    &-bg {
      position: absolute;
      right: 0;
      left: 0;
      margin: 0 auto;
      max-width: 85%;
    }
  }
}

@media (max-width: 576px) {
  .plug-mobile {
    padding-top: 80px;

    .container {
      padding-left: 30px;
      padding-right: 30px;
    }

    &-header {
      padding-top: 18px;
      padding-bottom: 18px;
      position: absolute;
      left: 0;
      right: 0;
      top: 0;
    }

    &-title {
      @include fz(40);
      line-height: 1.05;
      margin-bottom: 25px;
    }
    &-subtitle {
      @include fz(20);
    }
  }
}

@media (max-width: 576px) and (orientation: landscape) {
  .plug-mobile {

    &-title {
      @include fz(24);
      line-height: 1.1;
      margin-bottom: 16px;
    }
    &-subtitle {
      @include fz(16);
    }
  }
}
@media (max-width: 420px) {
  .plug-mobile {

    &-title {
      @include fz(24);
      line-height: 1.1;
      margin-bottom: 16px;
    }
    &-subtitle {
      @include fz(16);
    }
  }
}

//cookie popup
.popup-cookie {
  position: fixed;
  bottom: 50px;
  right: 50px;
  z-index: 99;
  padding: 28px 34px;
  max-width: calc(100% - 100px);
  width: 320px;
  @include fz(18);
  line-height: 1.5;
  background-color: $primary;
  color: #FFF;

  &.confirmed {
    transition: transform .4s, opacity .4s;
    transform: translate(0px, 50px);
    opacity: 0;
  }
  
	p {
		margin-bottom: 23px;
	}

	&-accept {
		position: relative;
		display: flex;
		padding: 12px 14px 12px 20px;
		@include fz(18);
		font-weight: 600;
		color: #fff;
		background-color: transparent;
		border: 2px solid #fff;
		border-radius: 10px;
    transition: .4s background-color, .4s color;
    cursor: pointer;

    &:hover {
			color: $primary;
			background-color: #fff;

			.svg-cookie {
				stroke: $primary;
			}
		}

		.svg-cookie {
			width: 24px;
			height: 24px;
			margin-left: 45px;
			fill: none;
			stroke: #fff;
      stroke-width: 2px;
		}
	}
}