.wrapper a img { border: none; display: block; line-height: 0; font-size: 0; }

.sprite, .svg-sprite { display: block; max-width: 100%; max-height: 100%; -webkit-transition: fill .4s, stroke .4s; -moz-transition: fill .4s, stroke .4s; -ms-transition: fill .4s, stroke .4s; -o-transition: fill .4s, stroke .4s; transition: fill .4s, stroke .4s; }

