.ui-btn { display: inline-block; vertical-align: middle; max-width: 100%; height: 76px; line-height: 74px; white-space: nowrap; text-align: center; padding: 0px 30px; border-radius: 8px; background-color: transparent; border: 1px solid #FFF; font-family: "TT Hoves"; color: #FFF; text-transform: none; font-size: 24px; font-size: 1.5rem; font-weight: 400; -ms-transition: border .3s, color .3s; transition: border .3s, color .3s; cursor: pointer; outline: none; }

